import React from 'react'
import HTMLFlipBook from "react-pageflip";

const PageCover = React.forwardRef((props, ref) => {
  return (
    <div className="page page-cover" ref={ref} data-density="hard" >
      <div className="page-content" >
        <h2>{props.children} </h2>
      </div>
    </div>
  );
});

const Page = React.forwardRef((props, ref) => {
  return (
    <div className="page" ref={ref}>
      <div className="page-content">
        <h2 className="page-header"> Page header - {props.number} </h2>
        {(props.image !== undefined) ?
          <div
            className="page-image"
            style={{ backgroundImage: "url(" + props.image + ")" }}
            onClick={showFullImage}
          ></div> : null
        }
        <div className="page-text"> {props.children} </div>
        <div className="page-footer"> {props.number + 1} </div>
      </div>
    </div>
  );

  function showFullImage() {
    console.log('click');
  }
});

class DemoBook extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      page: 0,
      totalPage: 0,
    };
  }

  nextButtonClick = () => {
    this.flipBook.getPageFlip().flipNext();
  };

  prevButtonClick = () => {
    this.flipBook.getPageFlip().flipPrev();
  };

  renderFullScreen = () => {
    document.getElementsByTagName('HTMLFlipBook')[0].requestFullscreen();
  }

  onPage = (e) => {
    this.setState({
      page: e.data,
    });
  };

  onChangeOrientation = (e) => {
    this.setState({
      orientation: e.data,
    });
  }

  onChangeState = (e) => {
    this.setState({
      state: e.data,
    });
  }

  componentDidMount() {
    this.setState({
      totalPage: this.flipBook.getPageFlip().getPageCount(),
    });
  }

  render() {
    return (
      <div className="no-select" style={{ maxHeight: '100vh', maxWidth: '100vw', display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
        <HTMLFlipBook
          width={500}
          height={700}
          // minWidth={315}
          // maxWidth={1000}
          // minHeight={400}
          // maxHeight={1533}
          // size="stretch"
          maxShadowOpacity={0.5}
          showCover={true}
          mobileScrollSupport={true}
          useMouseEvents={false}
          drawShadow={true}
          onFlip={this.onPage}
          onChangeOrientation={this.onChangeOrientation}
          onChangeState={this.onChangeState}
          className="image-book flip-book html-book"
          ref={(el) => (this.flipBook = el)}
        >

          <PageCover>Alim Adilov képeskönyve</PageCover>
          <Page number={1} image={process.env.PUBLIC_URL + "/h608.jpg"}>
            <span>Lorem ipsum...</span>
          </Page>
          <Page number={2}> Lorem ipsum...</Page>
          <Page number={3}> Lorem ipsum...</Page>
          <Page number={4}> Lorem ipsum...</Page>
          <PageCover> THE END </PageCover>

        </HTMLFlipBook>

        <div className="pageturner-container">
          <div>

            <button type="button" onClick={this.prevButtonClick}>Previous page</button>

            [<span>{this.state.page}</span> of <span> {this.state.totalPage} </span>]

            <button type="button" onClick={this.nextButtonClick}>Next page</button>

            <button type="button" onClick={this.renderFullScreen}>Teljes képernyős mód</button>
          </div>
        </div>
      </div>
    );
  }
}

export default DemoBook;